.about-subject {
	font-size: 14px;
	text-align: right;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.about-sub_title,
.about-sub_name {
	border-bottom: 1px solid white;
	font-size: 16px;
	margin-bottom: 20px;
	padding-bottom: 10px;
}

.about-sub_name {
	@media (min-width: 768px) {
		display: none;
	}
}

.about-image {
	display: none;

	@media (min-width: 768px) {
		display: flex;
		position: relative;
		justify-content: flex-end;
	}
}

.about-image img,
.about-image_name {
	width: 272px;

	@media (min-width: 1024px) {
		width: 340px;
	}

	@media (min-width: 1280px) {
		width: 272px;
	}
}

.about-image_name {
	bottom: 20px;
	font-size: 24px;
	position: absolute;
	text-align: center;
}

.about-row {
	background: rgba(0, 0, 0, 0.8);
}
