body {
	background: black;
	color: white;
}

.backImage {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: top center;
	height: calc(100vh - 69px);
	position: fixed;
	overflow-y: auto;

	@media (min-width: 577px) {
		background-position: center center;
		height: calc(100vh - 91px);
	}
}
